import { createApp } from "vue";
import App from "./App.vue";
import "./assets/tailwind.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas, faCode, faUser} from '@fortawesome/free-solid-svg-icons';
import { faVuejs, faGithub, faLinkedin, faFacebook, faFigma} from '@fortawesome/free-brands-svg-icons';
// import {faUser} from '@fontawesome/free-duotone-svg-icons';
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

library.add(fas, faVuejs, faGithub, faLinkedin, faFacebook, faFigma, faCode, faUser);


const app = createApp(App);
app.component("font-awesome-icon", FontAwesomeIcon);
app.mount("#app");
